import React, { lazy, Suspense } from 'react'
import { connect } from 'react-redux'
import { Navigate } from 'react-router-dom';
import Layout from '../utils/layout'
import SpinnerComponent from "../utils/spinner"

import { GET_SALES_LIST_ACTION, UPDATE_GET_SALES_LIST_ACTION } from "../../redux/actions/actions"
// import SalesTable from './sales_table';
// import SalesDateSelector from './sales_date_selector';

const SalesTable = lazy(() => import('./sales_table'));
const SalesDateSelector = lazy(() => import('./sales_date_selector'));

class SalesPage extends React.Component {
    state = {
        spinner: false,
        redirect: false,
        sales_date: this.props.sales_date
    }
    static getDerivedStateFromProps = async (props, state) => {
        const { token, sales_list, sales_date, update_get_sales_list } = props
        if (token === undefined || token === null) {
            state.redirect = true
            return state
        }
        if (update_get_sales_list) {
            state.spinner = true
            state.sales_date = sales_date
            // await props.GET_SALES_LIST_ACTION(sales_date.start_date, sales_date.end_date)
            return state
        }
        else {
            state.spinner = false
            state.redirect = false
        }

        return state
    }
    componentDidMount = async () => {
        const { sales_date, sales_list, token } = this.props
        if (sales_list === undefined || sales_list === null) {
            this.setState({ spinner: true })
            await this.props.UPDATE_GET_SALES_LIST_ACTION()
            await this.props.GET_SALES_LIST_ACTION(token.depot, sales_date.start_date, sales_date.end_date)
        }
    }
    render() {
        if (this.state.redirect) {
            return <Navigate to={"/login"} />
        }
        console.log(this.props.sales_list)

        return (
            <Layout header_title={"Sales"}>
                {this.state.spinner ? <SpinnerComponent spinner={this.state.spinner} /> :
                    <div>
                        <Suspense fallback={<SpinnerComponent spinner={true} />}>
                            <SalesDateSelector />
                        </Suspense>
                        <Suspense fallback={<SpinnerComponent spinner={true} />}>
                            <SalesTable sales_list={this.props.sales_list} from_customer={false} />
                        </Suspense>
                    </div>}

            </Layout>
        )
    }
}

const mapStateToProps = state => {
    return {
        token: state.auth_reducer.token,
        sales_list: state.list_reducer.sales_list,
        update_get_sales_list: state.list_reducer.update_get_sales_list,
        sales_date: state.update_reducer.sales_date,
    };
};

const mapDispatchToProps = {
    GET_SALES_LIST_ACTION,
    UPDATE_GET_SALES_LIST_ACTION,
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(SalesPage);
